$blue: #008bd0;
$blue-gradient: #008bd012;

.dropdown-item {
    font-size: 14px;
}
.page-wrapper.compact-wrapper .page-body-wrapper header.main-nav .main-navbar .nav-menu > li .nav-link.active, .page-wrapper.compact-wrapper .page-body-wrapper header.main-nav .main-navbar .nav-menu > li .nav-link:hover, .btn-primary, .badge-primary{
    background-color: $blue !important;
}

.btn-primary .btn-outline-primary {
    border-color: $blue !important;
}

.page-wrapper.compact-wrapper .page-body-wrapper header.main-nav .main-navbar .nav-menu > li .nav-submenu li a,
.btn-outline-primary{
    color: $blue !important;
}

.table-hover tbody tr:hover th, 
.table-hover tbody tr:hover td,
.btn-primary-light,
.page-main-header .main-header-right .nav-right ul li svg,
.page-wrapper .page-body-wrapper .page-header .breadcrumb .breadcrumb-item a,
.page-main-header .main-header-right .toggle-sidebar svg,
a,
.login-form .form-group .input-group-text
{
    color: $blue;
}

.table-hover tbody tr:hover,
.btn-primary-light,
.page-main-header .main-header-right .toggle-sidebar:hover,
.login-form .form-group .input-group-text,
.login-card{
    background-color: $blue-gradient !important;
}

.cursor-pointer {
    cursor: pointer;
}

@media(min-width: 480px) {
    .logo-wrapper{
        .img-fluid {
            max-width: 50%;
        }
    }
}